import React from 'react'

import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Button from '../../../components/Button'
import BackgroundImage from '../../../components/BackgroundImage'

import care_bg from './care_bg.png'
import { responsive } from '../../../contexts/responsive'

const Care = ({ fullpageApi }) => {
  return (
    <Box className="section">
      <BackgroundImage src={care_bg} height="100vh" ratio={1920 / 1080}>
        <Box color="white" mt={responsive('4em', '10em')} mx={responsive('auto', 'unset')} px={responsive('1em', '8.875em')} maxWidth={responsive('20.75em', '64em')}>
          <Text fontWeight={'black'} letterSpacing={responsive('0.25em', '0.5rem')} fontSize={responsive('1.875em', '3.75em')} mr={responsive(0, '3em')}></Text>
          <Text mt={responsive('0.75rem', '0.33em')} fontSize={responsive('1em', '1.625em')}>
            <br />
            <br />
            
          </Text>
          <Box mt="1.5em">
            <Button.Outline
              fontSize={responsive('1em', '2.25em')}
              px={responsive('0.875em', '1em')}
              py={responsive('0.5em', '0.4em')}
              onClick={() => fullpageApi.moveSectionDown()}
            ></Button.Outline>
          </Box>
        </Box>
      </BackgroundImage>
    </Box>
  )
}

export default Care
